import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { TopLinksPage } from './top-links.page';

const routes: Routes = [
  {
    path: '',
    component: TopLinksPage
  },
  {
    path: 'career-plan',
    loadChildren: () => import('./career-plan/career-plan.module').then( m => m.CareerPlanPageModule)
  },  {
    path: 'services',
    loadChildren: () => import('./services/services.module').then( m => m.ServicesPageModule)
  },
  {
    path: 'study-cost',
    loadChildren: () => import('./study-cost/study-cost.module').then( m => m.StudyCostPageModule)
  },
  {
    path: 'scholarship',
    loadChildren: () => import('./scholarship/scholarship.module').then( m => m.ScholarshipPageModule)
  },
  {
    path: 'contact',
    loadChildren: () => import('./contact/contact.module').then( m => m.ContactPageModule)
  },
  {
    path: 'moksh-pro',
    loadChildren: () => import('./moksh-pro/moksh-pro.module').then( m => m.MokshProPageModule)
  },
  {
    path: 'moksh-plus',
    loadChildren: () => import('./moksh-plus/moksh-plus.module').then( m => m.MokshPlusPageModule)
  },
  {
    path: 'cmp',
    loadChildren: () => import('./cmp/cmp.module').then( m => m.CmpPageModule)
  },
  {
    path: 'vlogs',
    loadChildren: () => import('./vlogs/vlogs.module').then( m => m.VlogsPageModule)
  }

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class TopLinksPageRoutingModule {}
